import React, { Component } from "react";
import Loader from "react-loader-spinner";
import MatchesCarousel from "@justinc/matches-carousel-component";
import request from "superagent";
import superagentCachePlugin from "superagent-cache-plugin";
import cacheModule from "cache-service-cache-module";

const cache = new cacheModule({ storage: "session", defaultExpiration: 120 });
const superagentCache = superagentCachePlugin(cache);

class App extends Component {
  state = {
    isLoading: true,
    liveEvents: []
  };

  static styles = {};

  static loaderStyles = {};

  componentDidMount() {
    request
      .get(
        "http://api.unicdn.net/v1/feeds/sportsbook/event/live.jsonp?app_id=ca7871d7&app_key=5371c125b8d99c8f6b5ff9a12de8b85a"
      )
      .use(superagentCache)
      .end((err, res) => {
        if (err) {
          console.error(err.message);
          return;
        }
        const response = JSON.parse(res.text);
        this.setState({
          isLoading: false,
          liveEvents: response.liveEvents
        });
      });
  }

  render() {
    const { isLoading, liveEvents } = this.state;

    return isLoading ? (
      <div style={{ position: "relative" }}>
        <div
          style={{
            textAlign: "center",
            position: "absolute",
            left: "35%",
            transform: "translate(0, 50%)"
          }}
        >
          <Loader
            type="Ball-Triangle"
            color="#2e7423"
            height="100"
            width="100"
          />
        </div>
      </div>
    ) : (
      <div style={{ position: "relative" }}>
        <div
          style={{
            position: "absolute",
            width: "570px",
            top: "110px",
            transform: "translate(0, 50%)"
          }}
        >
          <MatchesCarousel
            data={liveEvents}
            iconPaths={{
              BASKETBALL: "images/icons/basketball.png",
              DEFAULT: "images/icons/default.png",
              FOOTBALL: "images/icons/football.png",
              TENNIS: "images/icons/tennis.png"
            }}
          />
        </div>
      </div>
    );
  }
}

export default App;
